import keycloak from "keycloak-js";
import { getLogger } from '@jitsi/logger';
//import qs from 'query-string';

const logger = getLogger(__filename);

//TODO:
//1. Allow object creation only once

export default class KeycloakAuth {
    static inst = undefined;
    url = "";
    configPath = "";
    isAuthenticated = false;
    queryString = "";
    constructor(url, configPath) {
        this.inst = new keycloak();
        //this.url = url;
        this.url = window.location.href;
        this.queryString = window.location.search;
        this.pathname = window.location.pathname;
        this.configPath = configPath;
        this.isAuthenticated = false;
    }


    check() {
        //check user already authenticated
        if(this.isAuthenticated) {
            return true;
        }

        //check the url containes jwt token
        var jwt = new URLSearchParams(this.queryString).get('jwt');
        //alert(jwt);

        //if user not yet authenticated redirect him to keycloak server
        this.inst.init({
            onLoad: 'login-required',
            PromiseType: "native",
            checkLoginIframe: false
        }).then(function (authenticated) {
            if(authenticated) {
                this.isAuthenticated = true;
                console.log('user authenticated succussfully !!!');
            } else {
                this.isAuthenticated = false;
                console.log('user not yet authencticated !!!!!');
            }
        }).catch(function () {
            //alert("keycloak initialization failed !!"); //TODO: yoga this object triggering this error each page refersh
        });
    }
}

Object.freeze(KeycloakAuth);

